import React from 'react'
import coder from "../images/coder.svg";
import Compo4 from './Compo4';



function Compo2() {
  return (
    <div className='coderimg'>
      <img src={coder} width='545px' />
      <div className='res'>
        <Compo4 />
      </div>
    </div>
    
  )
}

export default Compo2
