import React from 'react'
import imp from "../images/imp.svg";
import who from "../images/who.svg";
import part from "../images/part.svg";




function Footer() {
  return (
    <div>
      <div className="foot">
        <ul>
           <a href="#"> How We Import </a>
           <img src={imp} width='245px' />
        </ul>
        <ul>
          <a href="#"> Who We Are </a>
          <img src={who} width='345px' />

        </ul>
        <ul>
           <a href="#"> Our Partners</a>
           <img src={part} width='245px' />

        </ul>
      </div>
      <div className='copyright'>
          <h5>© 2022 All Rights Reserved Vikas Private Ltd.</h5>
      </div>
    </div>
  )
}

export default Footer
