import * as React from 'react';
import Box from '@mui/material/Box';
import Paper from '@mui/material/Paper';


function Compomet() {
  return (
    <Box
      sx={{
        display: 'flex',
        flexWrap: 'wrap',
        color: 'black',
        '& > :not(style)': {
          m: 1,
          width: 428,
          height: 428,
        },
      }}
    >
      <Paper elevation={24} />
    </Box>
  )
}

export default Compomet
